%btn_gradient{
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ff5800+0,ff8400+100 */
  background: rgb(255,88,0); /* Old browsers */
  background: -moz-linear-gradient(top,  rgba(255,88,0,1) 0%, rgba(255,132,0,1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  rgba(255,88,0,1) 0%,rgba(255,132,0,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  rgba(255,88,0,1) 0%,rgba(255,132,0,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff5800', endColorstr='#ff8400',GradientType=0 ); /* IE6-9 */
}
  

